.welcome {
  padding: 9.25rem 0 11.25rem;

  &__title {
    font-weight: bold;
    font-size: 6rem;
    line-height: 7.5rem;
    letter-spacing: 0.0625rem;
    text-align: center;

    span {
      color: var(--accent-color);
    }
  }

  &__advantages {
    display: grid;
    grid-column-gap: 5.3125rem;
    grid-template-columns: repeat(3, auto);
    align-content: center;
    justify-content: center;
    margin: 7.5rem auto 0;
  }

  &__advantages-item {
    &:first-of-type {
      padding-right: 3rem;
    }
  }

  &__contact-btn {
    margin: 9rem auto 0;
  }
}

.advantage {
  display: flex;
  align-items: center;
  color: inherit;
  text-decoration: none;
  opacity: 0.6;
  transition: all ease-out 0.15s;

  &:hover {
    opacity: 1;
  }

  &__left {
    margin-right: 1.125rem;
    font-weight: bold;
    font-size: 4.6009rem;
    line-height: 5.75rem;
  }

  &__right {
    font-weight: bold;
    font-size: 2.2835rem;
    line-height: 2.6875rem;

    img {
      max-height: 5.5rem;
    }
  }
}

.cases-section {
  padding: 3.75rem 0 7.375rem;

  &__title {
    font-weight: bold;
    font-size: 3rem;
    line-height: 3.75rem;
    letter-spacing: 0.0625rem;
    text-align: center;
  }

  &__grid {
    display: grid;
    grid-gap: 7.5rem;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 3.75rem;
  }

  &__item {
    &:nth-child(2n) {
      position: relative;
      top: 7.5rem;
    }
  }
}

.tags-slider {
  padding: 1.875rem 0;
  background: var(--accent-color);

  .swiper {
    padding: 0.9375rem;
  }

  .swiper-slide-active {
    transform: scale(1.25);
  }

  .swiper-slide-prev,
  .swiper-slide-next {
    transform: scale(1.15);
  }

  &__tag {
    display: inline-block;
    padding: 0 3.75rem;
    color: var(--background-color);
    font-weight: bold;
    font-size: 2.25rem;
    line-height: 3rem;
    text-decoration: none;
    transition: ease-out 0.16s;

    &.swiper-slide {
      width: auto;
    }

    &:hover {
      transform: scale(1.3);
    }
  }
}

.project-road {
  padding: 3.75rem 0;
  color: var(--background-color);
  background-color: var(--accent-color);

  &__title {
    font-weight: bold;
    font-size: 3rem;
    line-height: 3.75rem;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-column-gap: 1.5rem;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 3.75rem;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  &__icon {
    display: flex;
    align-items: center;
    height: 3.75rem;

    img {
      max-height: 100%;
    }
  }

  &__subtitle {
    margin-top: 1.5rem;
    font-weight: 500;
    font-size: 1.75rem;
    line-height: 2.25rem;
  }

  &__desc {
    max-width: max(15.3125rem, 65%);
    margin-top: 0.75rem;
    font-size: 1.125rem;
    line-height: 1.375rem;
  }
}

.testimonials {
  padding-bottom: 3.75rem;
  text-align: center;
  background-color: var(--bg-color);

  &__heading {
    color: var(--font-color);
    font-weight: 700;
    font-size: 3rem;
    line-height: 3.75rem;
    letter-spacing: 0.0625rem;
    text-align: center;
  }
}

.slider {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 60px;

  &__pagination {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 3.4375rem;
    height: 100%;
  }

  &__cards {
    flex: 1;
    margin: 0 2.25rem;
    padding-top: 2.5rem;
  }

  &__controls {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 3.4375rem;
    padding-top: 7.5rem;
    padding-left: 0.625rem;

    &_prev {
      width: 3.375rem;
      height: 3.375rem;
      background: url(../../icons/up.svg);
      background-color: none;
      background-size: contain;
      border: none;
      cursor: pointer;
    }

    &_next {
      width: 3.375rem;
      height: 3.375rem;
      background: url(../../icons/up.svg);
      background-color: none;
      background-size: contain;
      border: none;
      transform: rotate(-180deg);
      cursor: pointer;
    }
  }
  &__dot {
    z-index: 10;
    display: block;
    width: 0.1875rem;
    height: 3rem;
    margin: 0.375rem 0;
    background-color: rgba(#373737, 0.7);
    transition: 0.2s ease-out all;
    &_active {
      height: 29.875rem;
      background-color: var(--accent-color);
    }
  }
}

.slide-block {
  position: relative;
  height: 6.25rem;
  margin-top: -2.5rem;
  overflow: hidden;
  background-color: var(--background-color);
  border-radius: 0.9375rem;
  box-shadow: -0.0625rem 0.0625rem 0.5rem 0.1875rem rgba(#858585, 0.25);
  transition: 0.2s ease-out all;
  &__wrapper {
    position: relative;
    display: flex;
    padding-top: 3.75rem;
  }
  &_active {
    height: 30.625rem;
    .slide-block__wrapper {
      padding-top: 0px;
    }
  }
  &__image {
    width: 21.375rem;
    height: 23.125rem;
    margin: 3.75rem;
    overflow: hidden;
  }
  &__text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 34.375rem;
    height: 23.125rem;
    padding-top: 3.75rem;
    color: var(--font-color);
    text-align: left;
    img {
      width: 3rem;
      height: 3rem;
    }
    p {
      margin-top: 1.5rem;
      font-size: 1.125rem;
      line-height: 1.75rem;
    }
    h3 {
      margin-top: 3rem;
      font-weight: 500;
      font-size: 2.25rem;
      line-height: 3rem;
    }
    h4 {
      margin-top: 0.75rem;
      font-weight: 400;
      font-size: 1.125rem;
      line-height: 1.375rem;
    }
  }
}
